import Point from '@/model/Point';
import { FractalRenderer } from './FractalRenderer';
import { Gradient } from './Gradient';

export const BlendModes = {
   Normal: "normal",
   Multiply: "multiply",
   Screen: "screen",
   Lighten: "lighten",
   ColorDodge: "color-dodge",
   ColorBurn: "color-burn",
   HardLight: "hard-light",
   SoftLight: "soft-light",
   Subtract: "difference",
   Copy: "copy",
};

export const BackgroundTypes = {
   Color: "Color",
   Gradient: "Gradient",
   Image: "Image",
   Transparent: "Transparent"
};

export const ColorTypes = {
   Color: "Color",
   Gradient: "Gradient",
   IterationGradient: "Iteration Gradient",
};

export const LineWidthUnit = {
   Pixels: "px",
   Millimeters: "mm",
};

export interface BackgroundSettings{
   type: string;

   color: string;
   gradient?: Gradient;

   // For image type
   image?: HTMLImageElement; // TODO stuff like object-fit and object-position
   
   // For linear gradients
   gradientFrom?: Point;
   gradientTo?: Point;
}

export interface ColorSettings{
   type: string;

   color?: string;
   gradient?: Gradient;

   gradientFrom?: Point;
   gradientTo?: Point;

   gradientCache?: CanvasGradient;
}

export interface LineWidthSettings{
   width: number;
   unit: string;
}


export class FractalRenderSettings{
   // TODO make this a class that also allows gradients and whatnot (screen space and normal space, whatever)
   color: ColorSettings;
   backgroundColor: BackgroundSettings;

   blendMode: string;

   lineWidth: LineWidthSettings;

   constructor(){
      // Default color sceme
      // this.color = "#2f1c0810";
      // this.backgroundColor = "#f5e6ce";

      this.color =            { type: ColorTypes.Color, color: "#22222240" };
      this.backgroundColor =  { type: BackgroundTypes.Color, color: "#fff" };
      this.lineWidth =        { unit: LineWidthUnit.Pixels, width: 1 };

      this.blendMode = BlendModes.Normal;

      // this.backgroundColor =  { 
      //    type: BackgroundTypes.Gradient, 
      //    color: "#fff",
      //    gradient: new Gradient()
      //       .addStop(0, "fffaf0")//  
      //       .addStop(1, "f5efe4"), //
      //    gradientFrom: new Point(0, 10),
      //    gradientTo: new Point(0, -1),
      // };
      
      this.color =  { 
         type: ColorTypes.Color, 
         color: "#2e2c2840",
         // gradient: new Gradient()
         //    .addStop(0, "#2224")
         //    .addStop(1, "#fff4"),
         // gradientFrom: new Point(-1, -1),
         // gradientTo: new Point(1, 1),
      };


      // this.color = "#99dd3320";
      // this.backgroundColor = "#222";
      // this.blendMode = BlendModes.Add;
      
      // this.color = "#ff883310";
      // this.backgroundColor = "#222";
      // this.blendMode = BlendModes.Add;
      
      // this.color = "#FF149310";
      // this.backgroundColor = "#000";
      // this.blendMode = BlendModes.Add;

      // this.backgroundColor = "#297373";
      // this.color = "#FF855240";
   }

   public assign(other: FractalRenderSettings){
      this.color = other.color;
      this.backgroundColor = other.backgroundColor;
      this.blendMode = other.blendMode;
      this.lineWidth = other.lineWidth;
   }

   public applyColor(context: CanvasRenderingContext2D, normalizedIterations: number, from: Point, to: Point){
      // Set the color
      switch(this.color.type){
         case ColorTypes.Color:
            context.strokeStyle = this.color.color!;
            break;
         case ColorTypes.IterationGradient:
            context.strokeStyle = this.color.gradient!.evaluate(normalizedIterations);
            break;
         case ColorTypes.Gradient:
            if(this.color.gradientCache === undefined){
               this.color.gradientCache = this.color.gradient?.toCanvasGradient(
                  context,
                  this.color.gradientFrom!,
                  this.color.gradientTo!
               )
            }
            context.strokeStyle = this.color.gradientCache!;
            break;
      }

      context.fillStyle = context.strokeStyle;
   }
}