export class Signal{
   functions: (() => void)[];

   constructor(){
      this.functions = [];
   }

   listen(func: () => void){
      this.functions.push(func);
   }
   remove(func: () => void){
      let index = this.functions.indexOf(func);
      if(index < 0) return;

      this.functions.splice(index);
   }

   emit(){
      this.functions.forEach(func => func());
   }
}

interface BundledSignal{
   listener: (() => void);
   target: Signal;
}

export class SignalBundle{
   signals: BundledSignal[] = [];

   listenTo(target: Signal, listener: (() => void)){
      target.listen(listener);
      this.signals.push({target, listener})
   }

   removeAll(){
      this.signals.forEach(bundle => bundle.target.remove(bundle.listener));
   }
}