export default class Point{
   public x: number;
   public y: number;

   constructor(x?: number, y?: number){
      this.x = x || 0;
      this.y = y || 0;
   }

   clone(){
      return new Point(this.x, this.y);
   }

   assign(point: Point){
      this.x = point.x;
      this.y = point.y;
      
      return this;
   }

   normalize(){
      let l = this.length();

      this.x /= l;
      this.y /= l;

      return this;
   }

   length(){
      return Math.sqrt(
         this.x * this.x + this.y * this.y
      );
   }

   add(point: Point){
      this.x += point.x;
      this.y += point.y;
      return this;
   }

   mul(point: Point){
      this.x *= point.x;
      this.y *= point.y;
      return this;
   }

   sub(point: Point){
      this.x -= point.x;
      this.y -= point.y;

      return this;
   }

   div(point: Point){
      this.x /= point.x;
      this.y /= point.y;
      return this;
   }

   perp(){
      let temp = this.x;
      
      this.x = this.y;
      this.y = -temp;

      return this;
   }
   
   scale(scaler: number){
      this.x *= scaler;
      this.y *= scaler;
      return this;
   }

   distance(point: Point){
      return point.clone().sub(this).length();
   }

   dot(point: Point){
      return this.x * point.x + this.y * point.y;
   }

   lineDistanceSigned(from: Point, to: Point){
      let dir = to.clone().sub(from);
      let length = dir.length();

      dir.normalize();

      let perp = dir.clone().perp();

      let relativePoint = this.clone().sub(from);

      let dot = dir.dot(relativePoint);

      // If its not projected within the line segment
      if(dot < 0) return this.distance(from);
      if(dot > length) return this.distance(to);

      // Otherwise project over the other axis
      return perp.dot(relativePoint);
   }
   
   lineDistance(from: Point, to: Point){
      return Math.abs(this.lineDistanceSigned(from, to));
   }

   toString(){
      return "(" + Math.round(this.x * 100) / 100 + ", " + Math.round(this.y * 100) / 100 + ")";
   }
}