

















































import { Component, Prop, Vue } from 'vue-property-decorator';

import { FractalEditorState } from '@/model/FractalEditorState';
import Checkbox from '@/components/Checkbox.vue';
import Dropdown from '@/components/Dropdown.vue';
import { Shape } from '@/model/Fractal';
import Point from '@/model/Point';

@Component({
   components: { Checkbox, Dropdown },
})
export default class FractalEditorToolSelection extends Vue {
   private selectedTab = 0;

   @Prop() private state!: FractalEditorState;

   private toggleRecursive(rec: boolean){
      if(rec){
         this.state.selectedShape!.fractal = this.state.selectedFractal;
      }
      else{
         this.state.selectedShape!.fractal = undefined;
      }

      this.state.onFractalChange.emit();
   }

   private toggleDrawSelf(rec: boolean){
      this.state.selectedShape!.drawSelf = rec;

      this.state.onFractalChange.emit();
   }

   private flip(){
      this.state.selectedShape!.flip();
      this.state.onFractalChange.emit();
   }
   private del(){
      this.state.deleteSelected();
      this.state.onFractalChange.emit();
   }

   get isRecursive(){
      return this.state.hasSelectedShape
         && this.state.selectedShape!.fractal != undefined;
   }
   get hasDrawSelf(){
      return this.state.hasSelectedShape
         && this.state.selectedShape?.drawSelf;
   }

   private add(){
      let shape = new Shape(
         new Point(0, 0), new Point(0.5, 0)
      );
      this.state.selectedFractal!.shapes.push(shape);
      this.state.selectShape(shape);
   }
}

