

export class OutputSettings{
   public width: number;
   public height: number;

   public iterations: number;
   public dpi: number;

   public outputFormat: string = "png";

   constructor(){
      this.width = 1920;
      this.height = 1080;

      this.iterations = 14;
      this.dpi = 300;
   }

   public assign(other: OutputSettings){
      this.width = other.width;
      this.height = other.height;
      this.iterations = other.iterations;
      this.dpi = other.dpi;
   }
}