























import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import tinycolor from 'tinycolor2';

enum ColorPickerDragTarget{
   None,
   SaturationValue,
   Hue,
   Opacity
}

@Component
export default class ColorPicker extends Vue {
   public $refs!: { root: HTMLElement, satVal: HTMLElement, hue: HTMLElement, opacity: HTMLElement };

   @Prop({default:"red"}) private color!: string;

   private internalColor: tinycolor.Instance = tinycolor(this.color);
   private hsv: tinycolor.ColorFormats.HSVA = this.internalColor.toHsv();

   private drag: ColorPickerDragTarget = ColorPickerDragTarget.None;

   private open: boolean = false;
   
   @Watch("color")
   watcher(newColor: string){
      this.internalColor = tinycolor(newColor);
   }

   toggle(event: MouseEvent){
      if(event.target !== this.$refs.root) return;
      
      this.open = !this.open;
   }
   close(){
      this.open = false;
   }

   mouseUp(event: MouseEvent){
      this.drag = ColorPickerDragTarget.None;
   }
   mouseMove(event: MouseEvent){
      if(this.drag == ColorPickerDragTarget.SaturationValue){
         this.setSaturationValueMouseEvent(event);
      }
      if(this.drag == ColorPickerDragTarget.Hue){
         this.setHueMouseEvent(event);
      }
      if(this.drag == ColorPickerDragTarget.Opacity){
         this.setOpacityMouseEvent(event);
      }
   }
   mouseDown(event: MouseEvent){
      if(event.target === this.$refs.satVal){
         this.setSaturationValueMouseEvent(event);
         this.drag = ColorPickerDragTarget.SaturationValue;
         event.preventDefault();
      }
      if(event.target === this.$refs.hue){
         this.setHueMouseEvent(event);
         this.drag = ColorPickerDragTarget.Hue;
         event.preventDefault();
      }
      if(event.target === this.$refs.opacity){
         this.setOpacityMouseEvent(event);
         this.drag = ColorPickerDragTarget.Opacity;
         event.preventDefault();
      }
   }
   mouseLeave(event: MouseEvent){
      this.drag = ColorPickerDragTarget.None;
   }

   setSaturationValueMouseEvent(event: MouseEvent){
      let target = this.$refs.satVal;
      let rect = target.getBoundingClientRect();

      let x = event.pageX - rect.left;
      let y = event.pageY - rect.top;

      this.hsv.s = this.clamp01(x / rect.width);
      this.hsv.v = this.clamp01(1 - y / rect.height);

      this.sync();
   }

   setHueMouseEvent(event: MouseEvent){
      let target = this.$refs.hue;
      let rect = target.getBoundingClientRect();

      let x = event.pageX - rect.left;
      let y = event.pageY - rect.top;

      this.hsv.h = this.clamp01(x / rect.width) * 360;

      this.sync();
   }

   setOpacityMouseEvent(event: MouseEvent){
      let target = this.$refs.opacity;
      let rect = target.getBoundingClientRect();

      let x = event.pageX - rect.left;
      let y = event.pageY - rect.top;

      this.hsv.a = this.clamp01(x / rect.width);

      this.sync();
   }

   sync(){
      this.internalColor = tinycolor(this.hsv);
      this.$emit('update:color', this.rgba);
      this.$emit('change', this.rgba);
   }

   clamp01(value: number){
      if(value < 0) return 0;
      if(value > 1) return 1;
      return value;
   }

   get hex(){
      return this.internalColor.toHexString();
   }
   get rgba(){
      return this.internalColor.toRgbString();
   }
   set hex(hex: string){
      this.internalColor = tinycolor(hex);
   }

   get hue() { return this.hsv.h; }
   get saturation() { return this.hsv.s; }
   get value() { return this.hsv.v; }
   get opacity() { return this.hsv.a; }
   get alpha() { return this.opacity; }
}

