








import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class NumberSelector extends Vue {
  @Prop({default: 0}) private min!: number;
  @Prop({default: 10000}) private max!: number;
  @Prop({default: 0}) private value!: number;

  private internalValue: number = 0;

  public mounted(){
    this.internalValue = this.value;
  }

  public increase() {
    this.internalValue++;
    if(this.internalValue > this.max) this.internalValue = this.max;
    
    this.sync();
  }
  
  public decrease() {
    this.internalValue--;
    if(this.internalValue < this.min) this.internalValue = this.min;
    
    this.sync();
    
  }

  public sync(){
    this.$emit('update:value', this.internalValue);
  }
}

