import { FractalRenderSettings } from '@/renderer/FractalRenderSettings';
import { RenderWindow } from '@/renderer/RenderWindow';
import Fractal from './Fractal';

export class Composite{
   fractal?: Fractal;
   
   settings: FractalRenderSettings;
   window: RenderWindow;

   constructor(){
      this.fractal = undefined;
      
      this.settings = new FractalRenderSettings();
      this.window = new RenderWindow();
   }

   // Don't use these?
   public setFractal(fractal: Fractal){  
      this.fractal = fractal;
   }
   public setSettings(settings: FractalRenderSettings){  
      this.settings = settings;
   }
}