







import { FractalEditorState } from '@/model/FractalEditorState';
import { Component, Prop, Vue } from 'vue-property-decorator';
import FractalWindow from './FractalWindow.vue';
import FractalCompositeSettings from '@/views/FractalCompositeSettings.vue';
import AppModel from '@/model/AppModel';
import ColorPicker from '@/components/ColorPicker.vue';



@Component({components: { FractalWindow, FractalCompositeSettings }})
export default class FractalComposite extends Vue {
   private state = new FractalEditorState();

   beforeMount(){
      this.state.selectedFractal = AppModel.fractals[0];
      this.state.showVertices = false;
      this.state.showGrid = true;
      this.state.showCanvas = true;
      this.state.selectedComposite = AppModel.composite;
      this.state.iterations = 10; // TODO auto decide the amount of iterations
   }
}

