













import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class Dropdown extends Vue {

   @Prop({default: []}) private list!: string[];
   @Prop({default: ""}) private selected!: string;

   private open: boolean = false;

   private internalSelected: string = "";

   toggle(){
      this.open = !this.open;
   }
   close(){
      this.open = false;
   }

   select(index: number){
      this.internalSelected = this.list[index];
      this.$emit('update:selected', this.internalSelected);
      this.$emit('change', this.internalSelected);
   }

   public mounted(){
      this.internalSelected = this.selected;
   }
   
   private capitalize(value: string) {
      if (!value) return '';

      value = value.toString();

      return value.charAt(0).toUpperCase() + value.slice(1);
   }
}

