






















































import { BackgroundSettings, BackgroundTypes } from '@/renderer/FractalRenderSettings';
import { Component, Prop, Vue } from 'vue-property-decorator';
import Dropdown from "./Dropdown.vue";
import GradientPicker from "./GradientPicker.vue";
import ColorPicker from '@/components/ColorPicker.vue';
import { createGradient, Gradient } from '@/renderer/Gradient';
import Point from '@/model/Point';

@Component({
   components: { Dropdown, ColorPicker, GradientPicker }
})
export default class BackgroundStylePicker extends Vue {
   public $refs!: { fileUpload: HTMLInputElement };

   private types = BackgroundTypes;

   private selected = BackgroundTypes.Color;

   private gradient = new Gradient();

   private imgName = "";
   private imageSrc = "";

   @Prop() private settings!: BackgroundSettings;

   private internalSettings!: BackgroundSettings;

   beforeMount(){
      this.internalSettings = this.settings;
      this.gradient = createGradient("#000", "#fff");

      if(this.internalSettings.gradient === undefined){
         this.internalSettings.gradient = this.gradient;
      }
      else{
         this.gradient = this.internalSettings.gradient;
      }
      
      if(this.internalSettings.gradientTo === undefined){
         this.internalSettings.gradientTo = new Point(1, -1);
      }

      if(this.internalSettings.gradientFrom === undefined){
         this.internalSettings.gradientFrom = new Point(-1, 1);
      }

      if(this.internalSettings.image !== undefined){
         this.imageSrc = this.internalSettings.image.src;
      }

      this.selected = this.internalSettings.type;
   }

   upload(){
      this.$refs.fileUpload.click();
   }

   fileAdded(){
      if(this.$refs.fileUpload.files === null) return;
      if(this.$refs.fileUpload.files === undefined) return;
      if(this.$refs.fileUpload.files.length <= 0) return;

      let file = this.$refs.fileUpload.files[0];
      
      this.imgName = file.name;
      
      var reader = new FileReader();
      reader.onload = (event: ProgressEvent<FileReader>) => {
         let img = new Image();

         this.imageSrc = <string>event.target?.result;

         img.onload = ()=>{
            this.$emit('change', this.internalSettings);
         };

         img.src = this.imageSrc;

         this.internalSettings.image = img;
      }
      reader.readAsDataURL(file);
   }

   setColor(color: string){
      this.internalSettings.color = color;
      
      this.$emit('change', this.internalSettings);
   }

   setGradient(gradient: Gradient){
      this.gradient = gradient.clone();
      this.internalSettings.gradient = this.gradient;
      this.$emit('change', this.internalSettings);
   }

   selectType(type: string){
      this.selected = type;
      this.internalSettings.type = type;
      this.$emit('change', this.internalSettings);
   }
}

