

































































































import { Component, Prop, Vue } from 'vue-property-decorator';

import NumberSelector from '@/components/NumberSelector.vue';
import { OutputSettings } from '@/model/OutputSettings';
import AppModel from '@/model/AppModel';
import { estimateSteps, FractalRenderer } from '@/renderer/FractalRenderer';
import App from '@/App.vue';
import Checkbox from '@/components/Checkbox.vue';

// Estimations
const KBPerPixel = 2200 / (1920*1080);
const StepsPerSecond = 60 * 1000;

@Component({
   components: { NumberSelector, Checkbox },
})
export default class FractalRender extends Vue {
   private output = new OutputSettings();

   public $refs!: { canvas: HTMLCanvasElement };

   private width: string = "";
   private height: string = "";

   private widthValid: boolean = true;
   private heightValid: boolean = true;

   private renderTab: boolean = false;

   private rendering: boolean = false;
   private autoDownload: boolean = false;

   private renderer?: FractalRenderer;

   private progress: number = 0;

   beforeMount(){
      // Yes we copy this over from the app model :)
      this.output = AppModel.outputSettings;

      // This is fine :)
      this.width = "" + this.output.width;
      this.height = "" + this.output.height;
   }

   onChange(){
      let regex = new RegExp(/^[0-9]*$/, "s");

      this.widthValid = regex.test(this.width);
      this.heightValid = regex.test(this.height);

      if(this.widthValid) this.output.width = parseFloat(this.width);
      if(this.heightValid) this.output.height = parseFloat(this.height);
   }

   startRender(){
      this.rendering = true;
      this.renderTab = true;

      const composite = AppModel.composite;

      this.canvas.width = this.output.width;
      this.canvas.height = this.output.height;
      // this.canvas = new OffscreenCanvas(this.output.width, this.output.height);
      let context = this.canvas.getContext("2d")!;
      
      this.renderer = new FractalRenderer(context!);
      this.renderer.settings = composite.settings;

      this.renderer.onRenderStep = this.onProgress.bind(this);
      this.renderer.onRenderDone = this.onDone.bind(this);

      context.resetTransform();

      // Set the scale for the fractal stuff:
      composite.window.applyToContext(this.canvas, context);

      this.renderer.drawBackground(composite.window.getBounds(
         this.output.width / this.output.height
      ));

      this.renderer.reset();
      
      this.renderer.settings.lineWidth.width = composite.window.getPixelSize(this.canvas.width, this.canvas.height);

      this.renderer.renderFractal(composite.fractal!, -1, 0, 1, 0, this.output.iterations);
   }

   cancelRender(){
      this.renderTab = false;
      this.rendering = false;

      this.renderer?.reset();
   }

   onProgress(n : number){
      this.progress = n;
   }

   async onDone(){
      if(this.canvas === undefined) return;

      this.rendering = false;

      if(this.autoDownload){
         await this.download();
      }
   }

   async download(){
      if(this.canvas === undefined) return;
      if(this.rendering) return;

      this.canvas.toBlob((blob)=>{
         let link = document.createElement("a");
         link.href = URL.createObjectURL(blob);
         link.download = "";
         link.click();
      });

   }

   get canvas(){
      return this.$refs.canvas;
   }

   get valid(){
      return this.widthValid && this.heightValid;
   }

   get durationEstimate(): string{
      let steps = estimateSteps(AppModel.composite.fractal!, this.output.iterations);
      let duration = steps / StepsPerSecond;

      const minuteInSeconds = 1 * 60; 
      const hourInSeconds = minuteInSeconds * 60;

      let hours = Math.floor(duration / hourInSeconds);
      duration -= hourInSeconds * hours;   
      
      let minutes = Math.floor(duration / minuteInSeconds);
      duration -= minutes * minuteInSeconds;

      let seconds = Math.floor(duration);

      let hoursString = "" + hours;
      let minutesString = "" + minutes;
      let secondsString = "" + seconds;

      if(hoursString.length < 2) hoursString = "0" + hoursString;
      if(minutesString.length < 2) minutesString = "0" + minutesString;
      if(secondsString.length < 2) secondsString = "0" + secondsString;

      return hoursString + ":" + minutesString + ":" + secondsString;
   }

   get fileSizeEstimate(): string{
      let kb = this.output.width * this.output.height * KBPerPixel;
      
      if(kb > 1000000){
         return Math.round(kb / 100000) / 10 + "GB";
      }
      else if(kb > 1000){
         return Math.round(kb / 100) / 10 + "MB";
      }
      else{
         return Math.round(kb * 10) / 10 + "KB";
      }
   }

   get backgroundColor() {
      if(!!AppModel.composite.settings.backgroundColor){
         return AppModel.composite.settings.backgroundColor;
      }
      else{
         return "#fff";
      }
   }
}

