
export default function drawGrid(context: CanvasRenderingContext2D, x: number, y: number, width: number, height: number, subdivisions?: number){
   if(subdivisions === undefined){
      subdivisions = 10;
   }

   context.beginPath();

   for(let i = 0; i <= subdivisions; i++){
      context.moveTo(x + i / subdivisions * width, y);
      context.lineTo(x + i / subdivisions * width, y + height);
   }
   
   for(let i = 0; i <= subdivisions; i++){
      context.moveTo(x, y + i / subdivisions * height);
      context.lineTo(x + width, y + i / subdivisions * height);
   }
   
   context.stroke();
}

export function drawRectangle(context: CanvasRenderingContext2D, x: number, y: number, width: number, height: number){
   
   context.beginPath();

   context.moveTo(x, y);
   context.lineTo(x + width, y);
   context.lineTo(x + width, y + height);
   context.lineTo(x, y + height);
   context.lineTo(x, y);
   
   context.stroke();
}
