import Point from '@/model/Point';
import Fractal, { Shape } from '@/model/Fractal';
import { FractalRenderSettings } from '@/renderer/FractalRenderSettings';
import { RenderWindow } from '@/renderer/RenderWindow';
import { Composite } from './Composite';
import { Signal } from '@/util/Signal';

// TODO make this not a singleton and make sure we give this to the renderer (so we can also create embedded editors for interactive tutorials and whatnot)
export class FractalEditorState {
   public showVertices = true;
   public showGrid = false;
   public showCanvas = false;

   public canEditVertices = false;
   public canEditCanvas = false;

   public iterations = 8;

   public selectedPoint?: Point = undefined;
   public selectedShape?: Shape = undefined;
   public selectedFractal?: Fractal = undefined;

   public selectedComposite?: Composite = undefined;

   // public editorRenderSettings: FractalRenderSettings;
   public editorWindow : RenderWindow;

   public gridX = 10;
   public gridY = 10;

   public onSelectedPointChange: Signal = new Signal();
   public onSelectedShapeChange: Signal = new Signal();
   public onSelectedFractalChange: Signal = new Signal();

   public onFractalChange: Signal = new Signal();
   
   constructor(){
      // this.editorRenderSettings = new FractalRenderSettings();
      this.editorWindow = new RenderWindow();
   }
   
   get hasSelectedComposite(): boolean{
      if(this.selectedComposite === undefined) return false;
      return true;
   }

   get hasSelectedFractal(): boolean{
      if(this.selectedFractal === undefined) return false;
      return true;
   }

   get hasSelectedShape(): boolean{
      return this.hasSelectedFractal && this.selectedShape !== null && this.selectedShape !== undefined;
   }

   get hasSelectedPoint(): boolean{
      return this.hasSelectedShape && this.selectedPoint !== null && this.selectedPoint !== undefined;
   }

   resetSelection(){
      this.selectedPoint = undefined;
      this.selectedShape = undefined;
   }

   deleteSelected(){
      if(this.hasSelectedShape){
         let shape = this.selectedShape!;

         this.selectedFractal!.removeShape(shape);
      }   
   }

   selectPoint(point?: Point){
      this.selectedPoint = point;
      this.onSelectedPointChange.emit();
   }

   selectShape(shape: Shape){
      this.selectedShape = shape;
      this.onSelectedShapeChange.emit();
   }

   selectFractal(fractal: Fractal){
      this.selectedFractal = fractal;
      this.onSelectedFractalChange.emit();
   }
}