























import { Component, Prop, Vue } from 'vue-property-decorator';
import ColorPicker from '@/components/ColorPicker.vue';
import Dropdown from '@/components/Dropdown.vue';
import { BackgroundSettings, BlendModes, ColorSettings, FractalRenderSettings } from '@/renderer/FractalRenderSettings';
import AppModel from '@/model/AppModel';
import ForegroundStylePicker from "@/components/ForegroundStylePicker.vue";
import BackgroundStylePicker from "@/components/BackgroundStylePicker.vue";
import { FractalEditorState } from '@/model/FractalEditorState';
import { SignalBundle } from '@/util/Signal';

@Component({components: { ColorPicker, Dropdown, BackgroundStylePicker, ForegroundStylePicker }})
export default class FractalCompositeSettings extends Vue {
   @Prop() private state!: FractalEditorState;

   private blendModes = BlendModes;

   private settings?: FractalRenderSettings;
   private bundle: SignalBundle = new SignalBundle();

   beforeMount(){
      this.settings = AppModel.composite.settings;
   }

   mount(){
      this.bundle.listenTo(this.state.onSelectedShapeChange, this.$forceUpdate.bind(this));
   }
   unmount(){
      this.bundle.removeAll();
   }
   
   setBackground(background: BackgroundSettings){
      this.settings!.backgroundColor = background;

      this.state.onFractalChange.emit();
      
   }
   setForeground(foreground: ColorSettings){
      this.settings!.color = foreground;

      this.state.onFractalChange.emit();
      
   }
   setBlendMode(mode: string){
      this.settings!.blendMode = mode;

      this.state.onFractalChange.emit();
   }
}

