






















import { Component, Prop, Vue } from 'vue-property-decorator';
import Checkbox from '@/components/Checkbox.vue';
import NumberSelector from '@/components/NumberSelector.vue';

import { FractalEditorState } from '@/model/FractalEditorState';

@Component({
   components: { Checkbox, NumberSelector },
})
export default class FractalEditorPreviewSettings extends Vue {
   private iterations: number = 4;

   @Prop() private state!: FractalEditorState;

   toggleShowVertices(state: boolean){
      this.state.showVertices = state;
      this.state.onFractalChange.emit();
   }
   toggleShowGrid(state: boolean){
      this.state.showGrid = state;
      this.state.onFractalChange.emit();
   }
   setIterations(it: number){
      this.state.iterations = it;
      this.state.onFractalChange.emit();
   }
}

