








import { Component, Prop, Vue } from 'vue-property-decorator';

import FractalEditorPreviewSettings from './FractalEditorPreviewSettings.vue';
import FractalEditorToolSelection from './FractalEditorToolSelection.vue';
import FractalWindow from './FractalWindow.vue';

import { FractalEditorState } from '@/model/FractalEditorState';
import AppModel from '@/model/AppModel';

@Component({
   components: { FractalEditorPreviewSettings, FractalWindow, FractalEditorToolSelection },
})
export default class FractalEditor extends Vue {
   private state = new FractalEditorState();

   beforeMount(){
      this.state.selectedFractal = AppModel.fractals[0];
      this.state.canEditVertices = true;
   }
}

