
import Point from '@/model/Point';
import Fractal, { BaseShape, transformPoint, Shape } from "../model/Fractal";
import { BackgroundTypes, BlendModes, FractalRenderSettings } from './FractalRenderSettings';
import { RenderWindow, RenderWindowBounds } from './RenderWindow';


export function drawFractal(context: CanvasRenderingContext2D, fractal: Fractal, x: number, y: number, x2: number, y2: number, iterations: number){
   let lastIteration = iterations <= 0;

   fractal.shapes.forEach(shape => {
      let from = transformPoint(x,y,x2,y2,shape.from);
      let to = transformPoint(x,y,x2,y2,shape.to);

      if(shape.drawSelf || shape.fractal == null || lastIteration){
         // Draw the self shape
         if(shape.baseShape === BaseShape.Line){
            context.moveTo(from.x, from.y);
            context.lineTo(to.x, to.y);
         }
         if(shape.baseShape === BaseShape.Circle){
            let centerX = (from.x + to.x) / 2;
            let centerY = (from.y + to.y) / 2;
            let radius = from.distance(to) / 2;

            context.moveTo(centerX + radius, centerY);

            context.arc(
               centerX, 
               centerY,
               radius,
               0, 
               Math.PI * 2
            );
         }
         else{
            // Draw nothing
            // console.log("Unsupported base shape: " + shape.baseShape);
         }
      }
      
      if(shape.fractal == null || lastIteration){
         return;
      }
      
      drawFractal(context, shape.fractal, from.x, from.y, to.x, to.y, iterations - 1);
   });
}

export function calculateSteps(fractal: Fractal|undefined, iterations: number){
   let count = 1;

   if(iterations <= 0) return count;
   if(fractal === undefined) return count;

   for(let i = 0; i < fractal.shapes.length; i++){
      count += estimateSteps(fractal.shapes[i].fractal, iterations - 1);
   }

   return count;
}

export function estimateSteps(fractal: Fractal|undefined, iterations: number): number{
   if(iterations <= 0) return 1;
   if(fractal === undefined) return 0;

   let shapesLength = 0;

   fractal.shapes.forEach(shape => {
      if(!!shape.fractal){
         shapesLength++;
      }
   });

   return Math.pow(shapesLength, iterations) + estimateSteps(fractal, iterations - 1);
}

export class FractalQueueItem{
   public fractal: Fractal;
   public iterations: number;

   public x: number;
   public y: number;
   public x2: number;
   public y2: number;

   constructor(fractal: Fractal, iterations: number, x: number, y: number, x2: number, y2: number){
      this.fractal = fractal;
      this.iterations = iterations;

      // Probably can be point
      this.x = x;
      this.y = y;
      this.x2 = x2;
      this.y2 = y2;
   }
}

export class FractalQueue{
   public queue: FractalQueueItem[];
   public length: number; 

   constructor(){
      this.queue = [];
      this.length = 0;

      for(let i = 0; i < 100; i++){
         this.queue.push(new FractalQueueItem(new Fractal(), 0, 0, 0, 0, 0));
      }
   }
   
   public pop(): FractalQueueItem|undefined {
      if(this.length <= 0) return undefined;

      this.length--;
      return this.queue[this.length];
   }
   
   enqueue(fractal: Fractal, x: number, y: number, x2: number, y2: number, iterations: number){
      let item: FractalQueueItem|undefined = undefined;

      if(this.length >= this.queue.length){
         item = new FractalQueueItem(fractal, iterations, x, y, x2, y2);
         this.queue.push(item);
      }
      else{
         item = this.queue[this.length];// = new FractalQueueItem(fractal, iterations, x, y, x2, y2);
      }

      item.fractal = fractal;
      item.iterations = iterations;

      item.x = x;
      item.y = y;
      item.x2 = x2;
      item.y2 = y2;

      this.queue[this.length] = item;

      this.length++;
   }
   clear(){
      this.length = 0;
   }
}

export class FractalRenderer{
   public context: CanvasRenderingContext2D;
   public queue: FractalQueue;

   public settings: FractalRenderSettings;

   public _animationFrame: number = -1;

   public onRenderDone?: () => void;
   public onRenderStep?: (progress: number) => void;
   
   private stepsDone: number = 0;
   private stepsEstimate: number = 1;

   private _fromPointBuffer = new Point();
   private _toPointBuffer = new Point();

   constructor(context: CanvasRenderingContext2D){
      this.context = context;

      this.queue = new FractalQueue();
      this.settings = new FractalRenderSettings();
   }
   
   renderFractal(fractal: Fractal, x: number, y: number, x2: number, y2: number, iterations: number){
      this.reset();
      this.enqueueFractal(fractal, x, y, x2, y2, iterations);

      this.stepsDone = 0;
      this.stepsEstimate = estimateSteps(fractal, iterations);

      this.draw();
   }


   reset(){
      if(this._animationFrame >= 0){
         clearTimeout(this._animationFrame);
      }
      
      this._animationFrame = -1;
      this.queue.clear();
   }

   enqueueFractal(fractal: Fractal, x: number, y: number, x2: number, y2: number, iterations: number){
      this.queue.enqueue(fractal, x, y, x2, y2, iterations);
   }

   popFractal(){
      if(this.queue.length === 0) return;

      let item = this.queue.pop()!; // DONT USE ITEM BECAUSE THIS GETS REUSED IN THE QUEUE AFTER POPPING.

      let fractal = item.fractal;
      let x = item.x;
      let y = item.y;
      let x2 = item.x2;
      let y2 = item.y2;
      let iterations = item.iterations;

      let lastIteration = item.iterations <= 0;

      // Based on length. Someday?
      // lastIteration = Math.sqrt(
      //    Math.pow(item.x2 - item.x, 2) + Math.pow(item.y2 - item.y, 2)
      //    ) < 10 || lastIteration;

      for(let i = 0; i < fractal.shapes.length; i++){
         let shape = fractal.shapes[i];

         let from = transformPoint(x, y, x2, y2, shape.from, this._fromPointBuffer);
         let to = transformPoint(x, y, x2, y2, shape.to, this._toPointBuffer);
   
         if(shape.drawSelf || shape.fractal == null || lastIteration){
            // Draw the self shape
            this.context.beginPath();

            this.settings.applyColor(this.context, this.stepsDone / this.stepsEstimate, from, to);

            if(shape.baseShape === BaseShape.Line){
               this.context.moveTo(from.x, from.y);
               this.context.lineTo(to.x, to.y);
            }
            if(shape.baseShape === BaseShape.Circle){
               let centerX = (from.x + to.x) / 2;
               let centerY = (from.y + to.y) / 2;
               let radius = from.distance(to) / 2;
   
               this.context.moveTo(centerX + radius, centerY);
   
               this.context.arc(
                  centerX, 
                  centerY,
                  radius,
                  0, 
                  Math.PI * 2
               );
            }

            this.context.stroke();
         }
         
         if(shape.fractal == null || lastIteration){
            continue;
         }

         this.enqueueFractal(shape.fractal, from.x, from.y, to.x, to.y, iterations - 1);
      }
   }

   drawBackground(bounds: RenderWindowBounds){
      this.context!.clearRect(
         bounds.x,
         bounds.y,
         bounds.width,
         bounds.height
      );

      const bg = this.settings.backgroundColor;
   
      switch(bg.type){
         case BackgroundTypes.Color:
            this.context!.fillStyle = bg.color!;
            this.context!.fillRect(
               bounds.x,
               bounds.y,
               bounds.width,
               bounds.height
            );
            break;
         case BackgroundTypes.Transparent:
            this.context!.clearRect(
               bounds.x,
               bounds.y,
               bounds.width,
               bounds.height
            );
            break;
         case BackgroundTypes.Gradient:
            this.context!.fillStyle = 
               bg.gradient!.toCanvasGradient(
                  this.context!,
                  bg.gradientFrom!,
                  bg.gradientTo!
               );
            this.context!.fillRect(
               bounds.x,
               bounds.y,
               bounds.width,
               bounds.height
            );
            break;
         case BackgroundTypes.Image:
            this.context!.fillStyle = bg.color!;
            this.context!.fillRect(
               bounds.x,
               bounds.y,
               bounds.width,
               bounds.height
            );
            this.context!.drawImage(
               bg.image!,
               -1,
               1,
               2,
               -2
            );
            break;
      }

      
   }

   draw(){
      this._animationFrame = -1;

      let loops = 0;

      // this.context.strokeStyle = this.settings.color;
      this.context.lineWidth = this.settings.lineWidth.width;

      this.context.globalCompositeOperation = this.settings.blendMode;
      
      let max = Math.max(this.queue.length, 0);
      
      // this.context.beginPath();
      while(this.queue.length > 0 && loops < 1000){         
         // This is actually pretty inefficient. TODO make this better
         this.popFractal();
         
         max = Math.max(this.queue.length, 0);
         
         loops++;
         
         this.stepsDone++;
      }
      // this.context.stroke();

      this.context.globalCompositeOperation = BlendModes.Normal;

      if(this.onRenderStep !== undefined){
         this.onRenderStep(this.stepsDone / this.stepsEstimate * 100);
      }

      if(this.queue.length > 0){
         // this._animationFrame = requestAnimationFrame(this.draw.bind(this));
         this._animationFrame = setTimeout(this.draw.bind(this), 0);
      }
      else{
         if(this.onRenderDone !== undefined){
            this.onRenderDone();
         }
      }
   }
}