import Point from '@/model/Point';
import tinycolor from 'tinycolor2';

export class GradientStop{
   private _color: tinycolor.Instance;
   public position: number;

   constructor(position: number, color: string){
      this._color = tinycolor(color);
      this.position = position;
   }

   get color(){
      return this._color.toRgbString();
   }
   set color(color: string){
      this._color = tinycolor(color);
   }

   public getColor(){
      return this._color;
   }
}

export class Gradient{
   public stops: GradientStop[];

   constructor(){
      this.stops = [];
   }

   clone(){
      let g = new Gradient();

      this.stops.forEach(stop => {
         g.addStop(stop.position, stop.color);
      });

      return g;
   }
   
   sort(){
      this.stops.sort((a, b) => a.position - b.position);
   }

   normalize(){
      if(this.stops.length <= 1) return;

      this.sort();
      let length = this.stops[this.stops.length - 1].position;

      if(length === 0) return;

      this.stops.forEach(stop => stop.position /= length);
   }

   evaluate(f: number): string{
      if(this.stops.length === 1) return this.stops[0].color;

      let index = this.findIndex(f);

      let current = this.stops[index];      
      if(index + 1 >= this.stops.length) return current.color;
      let next = this.stops[index + 1];

      if(current.position === next.position) return current.color;

      let iv = (f - current.position) / (next.position - current.position);

      if(iv < 0) iv = 0;
      if(iv > 1) iv = 1;

      return tinycolor.mix(current.color, next.color, iv).toHexString();
   }

   findIndex(f: number): number{
      // TODO I could do binary search, but who cares :)
      for(let i = this.stops.length - 1; i > 0; i--){
         if(f > this.stops[i].position){
            return i;
         }
      }

      return 0;
   }
   addRawStop(stop: GradientStop){
      this.stops.push(stop);

      this.sort();

      return this;
   }

   addStop(position: number, color: string){
      this.stops.push(new GradientStop(position, color));
      return this;
   }
   removeStop(stop: GradientStop){
      let idx = this.stops.indexOf(stop);

      console.log("found stop at index : " + idx);
      
      if(idx < 0) return;

      this.removeStopByIndex(idx);
   }
   removeStopByIndex(index: number){
      console.log("splicing at index : " + index);
      this.stops.splice(index, 1);
   }

   toCanvasGradient(context: CanvasRenderingContext2D, from: Point, to: Point){
      let gradient = context.createLinearGradient(
         from.x,
         from.y,
         to.x,
         to.y
      );

      this.stops.forEach(stop => {
         gradient.addColorStop(stop.position, stop.color);
      });

      return gradient;
   }
}

export function createGradient(from: string, to: string): Gradient{
   let gradient = new Gradient();

   gradient.addStop(0, from);
   gradient.addStop(1, to);

   return gradient;
}