






































import { BackgroundSettings, BackgroundTypes } from '@/renderer/FractalRenderSettings';
import { Component, Prop, Vue } from 'vue-property-decorator';
import ColorPicker from '@/components/ColorPicker.vue';
import { createGradient, Gradient, GradientStop } from '@/renderer/Gradient';
import Point from '@/model/Point';

@Component({
   components: { ColorPicker }
})
export default class GradientPicker extends Vue {
   public $refs!: { canvas: HTMLCanvasElement };

   @Prop() private gradient!: Gradient;

   private self: Gradient = new Gradient();

   private context?: CanvasRenderingContext2D;

   private selectedStop: GradientStop|null = null;

   private padding: boolean = false;
   private dragging: boolean = false;
   private previousMousePosition: Point = new Point();

   beforeMount(){
      this.self = this.gradient.clone();
   }

   mounted(){
      this.context = this.canvas.getContext("2d")!;
      this.draw();
   }

   setColor(color: string){
      this.selectedStop!.color = color;
      this.$emit("change", this.self);
      this.draw();
   }

   draw(){
      if(this.context === undefined) return;

      this.canvas.width = this.canvas.offsetWidth;
      this.canvas.height = this.canvas.offsetHeight;

      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);

      let gradient = this.self.toCanvasGradient(
         this.context, 
         new Point(0, 0),
         new Point(this.canvas.width, 0));
      
      this.context.fillStyle = gradient;
      this.context.fillRect(0, 0, this.canvas.width, this.canvas.height);
   }

   private deleteStop(){
      if(this.self.stops.length <= 1) return;

      console.log("deleting...");

      this.self.removeStop(this.selectedStop!);
      this.selectedStop = null;
      
      this.$emit("change", this.self);
      this.draw();
   }

   private mouseEnter(event: MouseEvent){
   }
   private mouseLeave(event: MouseEvent){
      this.dragging = false;
   }
   private mouseMove(event: MouseEvent){
      let newPosition = this.mouseEventToPoint(event);

      let deltaX = newPosition.x - this.previousMousePosition.x;
      let deltaY = newPosition.y - this.previousMousePosition.y;

      if(this.dragging && this.selectedStop !== null){
         this.selectedStop.position = newPosition.x;
         this.self.sort();

         this.$emit("change", this.self);
         this.draw();
      }

      this.previousMousePosition = newPosition;
   }
   private mouseUp(event: MouseEvent){
      if(event.button === 0){
         this.dragging = false;
      }
   }
   private mouseDown(event: MouseEvent){
      this.canvas.focus();

      this.previousMousePosition = this.mouseEventToPoint(event);

      if(event.button === 0){
         this.dragging = this.select(this.previousMousePosition);

         this.draw();
      }
   }
   private doubleClick(event: MouseEvent){
      if(event.button === 0){
         let pos = this.mouseEventToPoint(event);

         if(pos.x < 0) pos.x = 0;
         if(pos.x > 1) pos.x = 1;
         this.selectedStop = new GradientStop(pos.x, this.gradient.evaluate(pos.x));
         
         this.self.addRawStop(this.selectedStop);
      }
   }

   select(point: Point){
      let maxDistance = 0.1;

      let selected: GradientStop|null = null;

      this.self.stops.forEach(stop => {
         let dist = Math.abs(stop.position - point.x);
         if(dist < maxDistance){
            maxDistance = dist;
            selected = stop;
         }
      });

      this.selectedStop = selected;

      return true;
   }

   mouseEventToPoint(event: MouseEvent): Point{
      return new Point(event.offsetX / this.canvas.width, 0);
   }

   get canvas(){
      return this.$refs.canvas;
   }

   get hasSelectedStop(){
      return this.selectedStop !== null;
   }
}

