import Point from '@/model/Point';

export class RenderWindowBounds{

   public top: number;
   public left: number;
   public bottom: number;
   public right: number;

   constructor(left: number, top: number, right: number, bottom: number){
      this.top = top;
      this.left = left;
      this.bottom = bottom;
      this.right = right;
   }

   get x(){
      return this.left;
   }

   get y(){
      return this.top;
   }

   get width(){
      return this.right - this.left;
   }
   
   get height(){
      return this.bottom - this.top;
   }
   
}

export class RenderWindow{
   // TODO aspect ratio
   // TODO helper functions for getting the coords to canvas
   // TODO rotation
   public focalPoint = new Point(0, 0);
   public viewScale = 1;

   // This shouldn't be used :)
   screenScale(width: number, height: number){
      return height / 2 / this.viewScale;
   }
   screenCenterX(width: number, height: number){
      let centerX = width / 2;
      centerX -=  this.screenScale(width, height) * this.focalPoint.x;
      return centerX;
   }
   screenCenterY(width: number, height: number){
      let centerY = height / 2;
      centerY +=  this.screenScale(width, height) * this.focalPoint.y;
      return centerY;
   }
   getPixelSize(width: number, height: number){
      return this.viewScale / height * 2;
   }

   applyToContext(canvas: HTMLCanvasElement|OffscreenCanvas, context: CanvasRenderingContext2D|OffscreenCanvasRenderingContext2D){
      context.translate(canvas.width / 2, canvas.height / 2);
      context.scale(canvas.height / 2, canvas.height / 2);
      context.scale(1 / this.viewScale, -1 / this.viewScale);
      context.translate(-this.focalPoint.x, -this.focalPoint.y);
   }

   get bounds(){
      return new RenderWindowBounds(
         this.focalPoint.x - this.viewScale,
         this.focalPoint.y + this.viewScale,

         this.focalPoint.x + this.viewScale,
         this.focalPoint.y - this.viewScale
      );
   }

   /**
    * @param aspectRatio As width / height
    */
   getBounds(aspectRatio: number){
      return new RenderWindowBounds(
         this.focalPoint.x - this.viewScale * aspectRatio,
         this.focalPoint.y + this.viewScale,

         this.focalPoint.x + this.viewScale * aspectRatio,
         this.focalPoint.y - this.viewScale
      );
   }
}