















import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

import AppModel from '@/model/AppModel';
import { drawFractal, FractalRenderer } from '@/renderer/FractalRenderer'; 
import { FractalEditorState } from '@/model/FractalEditorState'; 
import drawGrid, { drawRectangle } from '@/renderer/GridRenderer';
import Point from '@/model/Point';
import { transformPoint } from '@/model/Fractal';

import tinycolor from "tinycolor2";
import { SignalBundle } from '@/util/Signal';

@Component
export default class FractalWindow extends Vue {
   @Prop() private state!: FractalEditorState;

   public $refs!: { canvas: HTMLCanvasElement };
   private context?: CanvasRenderingContext2D;

   private panning: boolean = false;
   private dragging: boolean = false;

   private snap: boolean = false;

   private previousMousePosition: Point = new Point();
   
   private compositeScaling: boolean = false;
   private compositeWindowOffset: Point = new Point();

   private renderer?: FractalRenderer;

   private bundle: SignalBundle = new SignalBundle();

   public mounted() {
      this.context = this.$refs.canvas.getContext('2d')!;

      this.renderer = new FractalRenderer(this.context);
      this.renderer.settings = AppModel.composite.settings;

      this.onStateChange(this.state);

      this.draw();
   }

   public unmounted(){
      this.bundle.removeAll();
   }

   @Watch("state")
   public onStateChange(state: FractalEditorState){
      this.bundle.removeAll();

      if(this.state === undefined) return;

      this.bundle.listenTo(this.state.onSelectedPointChange, this.draw.bind(this));
      this.bundle.listenTo(this.state.onSelectedShapeChange, this.draw.bind(this));
      this.bundle.listenTo(this.state.onSelectedFractalChange, this.draw.bind(this));
      this.bundle.listenTo(this.state.onFractalChange, this.draw.bind(this));
   }
   
   // TODO maybe move this out of here into a different part of town :)
   private mouseEnter(event: MouseEvent){
   }
   private mouseLeave(event: MouseEvent){
      this.panning = false;
      this.dragging = false;
   }
   private mouseMove(event: MouseEvent){
      let newPosition = this.mouseEventToPoint(event);

      let deltaX = newPosition.x - this.previousMousePosition.x;
      let deltaY = newPosition.y - this.previousMousePosition.y;

      if(this.panning){
         this.state!.editorWindow.focalPoint.x -= deltaX;
         this.state!.editorWindow.focalPoint.y -= deltaY;

         // Because the view moves we need to move the actual point where the cursor is pointing now (actually, that hasn't changed)
         newPosition.x -= deltaX;
         newPosition.y -= deltaY;

         this.draw();
      }
      if(this.dragging && this.state!.hasSelectedPoint){
         this.state!.selectedPoint!.assign(newPosition);

         if(this.snap){
            this.state!.selectedPoint!.x = Math.round(this.state!.selectedPoint!.x * this.state!.gridX) / this.state!.gridX;
            this.state!.selectedPoint!.y = Math.round(this.state!.selectedPoint!.y * this.state!.gridY) / this.state!.gridY;
         }
         this.draw();
      }
      else if(this.dragging && this.state!.hasSelectedShape){
         // this.state!.selectedPoint!.assign(newPosition);
         let shape = this.state!.selectedShape!;
         shape.from.x += deltaX;
         shape.from.y += deltaY;
         shape.to.x += deltaX;
         shape.to.y += deltaY;

         // This doesn't really work tbh
         if(this.snap){
            let relativePoint = this.state!.selectedShape!.direction;
            
            shape.from.x = Math.round(shape.from.x * this.state!.gridX) / this.state!.gridX;
            shape.from.y = Math.round(shape.from.y * this.state!.gridY) / this.state!.gridY;

            shape.to.x = shape.from.x + relativePoint.x;
            shape.to.y = shape.from.y + relativePoint.y;
         }
         this.draw();
      }
      if(this.dragging && this.state!.hasSelectedComposite){
         let window = this.state!.selectedComposite!.window;

         if(this.compositeScaling){
            let correction = Math.sqrt(2);

            let offset = newPosition.clone().sub(window.focalPoint);

            window.viewScale = offset.length() / correction;
         }
         else{
            window.focalPoint.assign(newPosition).sub(this.compositeWindowOffset);

            console.log(window.focalPoint.toString());
   
            if(this.snap){
               window.focalPoint.x = Math.round(window.focalPoint.x * this.state!.gridX) / this.state!.gridX;
               window.focalPoint.y = Math.round(window.focalPoint.y * this.state!.gridY) / this.state!.gridY;
            }
         }

         this.draw();
      }
      
      this.previousMousePosition = newPosition;
   }
   private mouseUp(event: MouseEvent){
      if(event.button === 0){
         this.dragging = false;
      }
      if(event.button === 1){
         this.panning = false;
      }
   }
   private mouseDown(event: MouseEvent){
      this.canvas.focus();

      this.previousMousePosition = this.mouseEventToPoint(event);

      // console.log(this.previousMousePosition.toString());

      if(event.button === 0){
         this.dragging = this.select(this.previousMousePosition);

         this.draw();
      }
      if(event.button === 1){
         this.panning = true;
      }
   }
   private mouseWheel(event: MouseWheelEvent){
      if(event.deltaY < 0){
         this.state!.editorWindow.viewScale /= 1 / 0.9;
      }
      if(event.deltaY > 0){
         this.state!.editorWindow.viewScale /= 0.9;
      }
      this.draw();
   }

   private keyDown(event: KeyboardEvent){
      if(event.key === "Control"){
         this.snap = true;
      }
      if(event.key === "Delete"){
         this.state!.deleteSelected();
         this.draw();
      }
   }
   private keyUp(event: KeyboardEvent){
      if(event.key === "Control"){
         this.snap = false;
      }
   }

   // ============================================== //
   // Drawing code
   // ============================================== //
   draw(){
      let canvas = this.$refs.canvas;

      canvas.width = canvas.offsetWidth;
      canvas.height = canvas.offsetHeight;

      // Set identity matrix
      this.context!.resetTransform();
      this.state!.editorWindow.applyToContext(this.canvas, this.context!);

      this.renderer!.drawBackground(
         this.state!.editorWindow.getBounds(this.canvas.width / this.canvas.height));

      if(this.fractal !== undefined && this.renderer !== undefined){
         this.renderer.reset();

         // TODO remove this, and add it some otherplace
         this.renderer.settings.lineWidth.width = this.pixelSize;

         this.renderer.enqueueFractal(this.fractal, -1, 0, 1, 0, this.state!.iterations);
         this.renderer.draw();
      }

      // Debug window settings!
      if(false){
         this.drawDebug();
      }
      
      if(this.state!.showGrid){
         this.drawGrid();
      }
      if(this.state!.showVertices){
         this.drawVertices();
         this.drawStartingPositions();
      }

      // Composite stuff
      if(this.state!.hasSelectedComposite && this.state!.showCanvas){
         this.drawCanvas();
      }
   }

   drawDebug(){
      let canvas = this.$refs.canvas;
      let screenCenter = this.screenPointToWorldPoint(new Point(canvas.width / 2, canvas.height / 2));
      let focalPoint = this.state!.editorWindow.focalPoint;
      let mousePosition = this.previousMousePosition;

      let drawPoint = (point: Point)=>{
         this.context!.beginPath();
         this.context!.arc(point.x, point.y, this.pixelSize * 10, 0, Math.PI * 2, false);
         this.context!.stroke();
      };

      this.context!.lineWidth = this.pixelSize * 2;
      this.context!.strokeStyle = "red";
      drawPoint(focalPoint);

      this.context!.strokeStyle = "blue";
      drawPoint(screenCenter);

      this.context!.strokeStyle = "green";
      drawPoint(mousePosition);
   }

   drawGrid(){
      let isDark = tinycolor(this.renderer!.settings.backgroundColor.color).isDark();

      this.context!.strokeStyle = "#0000000f";
      
      if(isDark){
         this.context!.strokeStyle = "#ffffff0f";
      }

      this.context!.lineWidth = this.pixelSize;

      drawGrid(this.context!, -1, -1, 2, 2, 10);
   }

   drawVertices(){
      this.context!.globalCompositeOperation = "normal";

      let drawPoint = (point: Point)=>{
         this.context!.beginPath();
         this.context!.arc(point.x, point.y, this.pixelSize * 10, 0, Math.PI * 2, false);
         this.context!.stroke();
      };

      let x = -1;
      let y = 0;
      let x2 = 1;
      let y2 = 0;

      if(this.fractal === undefined) return;

      let isDark = tinycolor(this.renderer!.settings.backgroundColor.color).isDark();

      // Drawing the lines between the vertices
      this.fractal.shapes.forEach(shape => {
         let from = shape.from;
         let to = shape.to;

         let mid = from.clone().add(to).scale(0.5);
         let dir = to.clone().sub(from).normalize();
         let perp = dir.clone().perp();
         
         this.context!.beginPath();      
         this.context!.lineWidth = this.pixelSize;
         this.context!.strokeStyle = "black";
         
         if(isDark){
            this.context!.strokeStyle = "white";
         }

         if(this.state!.selectedShape == shape){
            this.context!.strokeStyle = "orange";
         }

         // The actual line
         this.context!.moveTo(from.x, from.y);
         this.context!.lineTo(to.x, to.y);

         // The arrow
         const scale = 8 * this.pixelSize;
   
         this.context!.moveTo(mid.x, mid.y);
         this.context!.lineTo(mid.x - dir.x * scale + perp.x * scale, mid.y - dir.y * scale + perp.y * scale);

         this.context!.moveTo(mid.x, mid.y);
         this.context!.lineTo(mid.x - dir.x * scale - perp.x * scale, mid.y - dir.y * scale - perp.y * scale);

         this.context!.stroke();
      });

      // Draw the vertices
      this.context!.strokeStyle = "orange";
      this.context!.lineWidth = this.pixelSize * 4;
      this.fractal.shapes.forEach(shape =>{
         // let from = transformPoint(x, y, x2, y2, shape.from);
         // let to = transformPoint(x, y, x2, y2, shape.to);
         
         this.context!.strokeStyle = "orange";
         
         // drawPoint(from);
         // drawPoint(to);

         drawPoint(shape.from);
         drawPoint(shape.to);
      });
      this.context!.stroke();
      
   }

   drawStartingPositions(){
      // Draw the starting points
      this.context!.strokeStyle = "blue";
      this.context!.lineWidth = this.pixelSize * 1;
      this.context!.beginPath();
      this.context!.arc(-1, 0, this.pixelSize * 30, 0, Math.PI * 2, false);
      this.context!.stroke();
      this.context!.beginPath();
      this.context!.arc(1, 0, this.pixelSize * 30, 0, Math.PI * 2, false);
      this.context!.stroke();
   }

   drawCanvas(){
      let isDark = tinycolor(this.renderer!.settings.backgroundColor.color).isDark();

      let context = this.context!;
      
      let composite = this.state!.selectedComposite!;

      this.context!.strokeStyle = "#0000000f";
      
      if(isDark){
         this.context!.strokeStyle = "#ffffff0f";
      }

      let scale = composite.window.viewScale;

      let x = composite.window.focalPoint.x;
      let y = composite.window.focalPoint.y;
      
      let asp = 16 / 9;
      
      // Drawing the background
      context.lineWidth = this.pixelSize;
      drawRectangle(context, x - scale, y - scale, scale * 2, scale * 2);
      drawRectangle(context, x - scale * asp, y - scale, scale * 2 * asp, scale * 2);
      
      // Drawing the corners
      {
         let cornerSize = scale / 5;

         context.strokeStyle = "#000000ff";
         if(isDark){
            context.strokeStyle = "#ffffffff";
         }
         context.lineWidth = this.pixelSize * 2;
   
         context.beginPath();
   
         // Top left
         context.moveTo(x - scale, y - scale);
         context.lineTo(x - scale + cornerSize, y - scale);
   
         context.moveTo(x - scale, y - scale);
         context.lineTo(x - scale, y - scale + cornerSize);
         
         // Bottom left
         context.moveTo(x - scale, y + scale);
         context.lineTo(x - scale + cornerSize, y + scale);
   
         context.moveTo(x - scale, y + scale);
         context.lineTo(x - scale, y + scale - cornerSize);
         
         // Bottom right
         context.moveTo(x + scale, y + scale);
         context.lineTo(x + scale - cornerSize, y + scale);
   
         context.moveTo(x + scale, y + scale);
         context.lineTo(x + scale, y + scale - cornerSize);

         // Top right
         context.moveTo(x + scale, y - scale);
         context.lineTo(x + scale - cornerSize, y - scale);
   
         context.moveTo(x + scale, y - scale);
         context.lineTo(x + scale, y - scale + cornerSize);
         
         // center
         context.moveTo(x - cornerSize / 2, y);
         context.lineTo(x + cornerSize / 2, y);
         
         context.moveTo(x, y - cornerSize / 2);
         context.lineTo(x, y + cornerSize / 2);
   
         context.stroke();
   
         context.lineWidth = this.pixelSize * 1;
      }
   }

   // ============================================== //
   // Helper code
   // ============================================== //
   public select(point: Point): boolean{
      const editorState = this.state!;

      // TODO fire the events correctly. This needs to be kinda rewritten

      // Selected composites. TODO make this behaviour plugable or something
      if(editorState.hasSelectedComposite){
         let window = editorState.selectedComposite!.window;
         let relativePoint = point.clone().sub(window.focalPoint);
         this.compositeWindowOffset.assign(relativePoint);

         this.compositeScaling = relativePoint.length() > window.viewScale;

         return true;
      }
      
      if(this.fractal === undefined) return false;
      if(editorState.canEditVertices === false) return false;

      const fractal = this.fractal;

      const maxDistance = this.pixelSize * 10;
      let distance = maxDistance;

      // Check if we need to select from the selected shape (that has prefference)
      if(editorState.hasSelectedShape){
         let fromDistance = point.distance(editorState.selectedShape!.from);
         let toDistance = point.distance(editorState.selectedShape!.to);

         let done = false;

         if(fromDistance < distance){
            editorState.selectedPoint = editorState.selectedShape!.from;
            editorState.selectedShape = editorState.selectedShape!;
            distance = fromDistance;
            done = true;
         }

         if(toDistance < distance){
            editorState.selectedPoint = editorState.selectedShape!.to;
            editorState.selectedShape = editorState.selectedShape!;
            distance = toDistance;

            done = true;
         }

         if(done){
            return true;
         }
      }

      // Then, reset the selection
      editorState.resetSelection();

      // Then select the fractal we really want to select I guess.
      fractal.shapes.forEach(shape => {
         let fromDistance = point.distance(shape.from);
         let toDistance = point.distance(shape.to);

         if(fromDistance < distance){
            editorState.selectedPoint = shape.from;
            editorState.selectedShape = shape;
            distance = fromDistance;
         }

         if(toDistance < distance){
            editorState.selectedPoint = shape.to;
            editorState.selectedShape = shape;
            distance = toDistance;
         }
      });

      if(editorState.hasSelectedPoint) return true;

      // Reset our clicking range again
      distance = maxDistance;

      // Try to select a shape instead
      fractal.shapes.forEach(shape => {
         let d = point.lineDistance(shape.from, shape.to);

         if(d < distance){
            editorState.selectedShape = shape;
            distance = d;
         }
      });

      if(editorState.hasSelectedShape) return true;
      
      return false;
   }

   // ============================================== //
   // Numbers
   // ============================================== //
   get fractal(){
      return this.state!.selectedFractal;
   }
   get canvas(){
      return this.$refs.canvas;
   }
   get pixelSize(){
      return this.state!.editorWindow.getPixelSize(this.canvas.width, this.canvas.height);
   }

   // Dont use these for drwaing
   get viewWidth(){
      // return (this.canvas.width / 2) * this.state!.editorWindow.viewScale;
      return this.state!.editorWindow.screenScale(
         this.canvas.width,
         this.canvas.height
      );
   }
   get viewX(){
      // let centerX = this.canvas.width / 2;
      // centerX +=  this.viewWidth * this.state!.editorWindow.focalPoint.x;
      // return centerX;
      return this.state!.editorWindow.screenCenterX(
         this.canvas.width,
         this.canvas.height
      );
   }
   get viewY(){
      // let centerY = this.canvas.height / 2;
      // centerY -=  this.viewWidth * this.state!.editorWindow.focalPoint.y;
      // return centerY;
      return this.state!.editorWindow.screenCenterY(
         this.canvas.width,
         this.canvas.height
      );
   }

   mouseEventToPoint(event: MouseEvent): Point{
      // y axis is reversed
      // return new Point((event.offsetX - this.viewX) / this.viewWidth, -(event.offsetY - this.viewY) / this.viewWidth);
      return this.screenPointToWorldPoint(new Point(event.offsetX, event.offsetY));
   }

   screenPointToWorldPoint(point: Point): Point{
      return new Point((point.x - this.viewX) / this.viewWidth, -(point.y - this.viewY) / this.viewWidth);
   }
}

