import Point from "./Point";

export enum BaseShape{
   None,
   Line,
   Circle,
   Square,
}

export class Shape{
   public from: Point;
   public to: Point;

   public drawSelf: boolean;
   public baseShape: BaseShape;

   public fractal?: Fractal;
   
   constructor(from: Point, to: Point, fractal?: Fractal){
      /** @type {Point} */
      this.from = from;
      /** @type {Point} */
      this.to = to;

      this.drawSelf = false;
      this.fractal = fractal;
      this.baseShape = BaseShape.Line; // Maybe make this more robust in some way?
   }

   flip(){
      let temp = this.from;
      this.from = this.to;
      this.to = temp;
   }
   
   get direction(){
      return this.to.clone().sub(this.from);
   }
}

export default class Fractal{
   public name: string;
   public shapes: Shape[];

   constructor(){
      // Each Fractal is in a bounding box like this:
      //
      // - - -1 - -
      // |    |    |
      //-1 - - - - 1
      // |    |    |
      // - -  1 - -
      // 
      // With 0, 0 in the center

      /** @type {Shape[]} */
      this.shapes = [ new Shape(new Point(-1, 0), new Point(1, 0)) ];
      this.name = "Fractal";
   }

   lineAmount(iterations: number){
      if(iterations == 0) return this.shapes.length;

      let recursion = 0;

      this.shapes.forEach(shape => {
         if(shape.fractal != null){
            recursion++;
         }
      });

      return Math.pow(recursion, iterations);
   }

   addShape(shape: Shape){
      this.shapes.push(shape);
   }
   removeShape(shape: Shape){
      let index = this.shapes.indexOf(shape);
      if(index < 0) return;

      this.shapes.splice(index, 1);
   }
}

/**
 * Transforms a point from fractal space into world space
 * @param {number} x 
 * @param {number} y 
 * @param {number} x2 
 * @param {number} y2 
 * @param {Point} point
 * @returns {Point} transformedPoint
 */
export function transformPoint(x: number, y: number, x2: number, y2: number, point: Point, output?: Point){
   let centerX = (x + x2) * 0.5;
   let centerY = (y + y2) * 0.5;

   let forwardX = x2 - centerX; // 1
   let forwardY = y2 - centerY; // 0

   let rightX = forwardY; // 0
   let rightY = -forwardX; // -1

   if(output === undefined){
      output = new Point();
   }

   // Why do we have to flip the y coordinate here? Thats a great question.
   // Maybe lets do some matrix math soon because I think thats when we figure it out
   // Y is up

   output.x = centerX + point.x * forwardX - point.y * forwardY,
   output.y = centerY + point.x * rightX - point.y * rightY
   
   return output;
}

export function createDefaultFractal(){
   let fractal = new Fractal();

   let shape1 = new Shape(
      new Point(-1, 0),
      new Point(0, 1),
      fractal
      );
      
   let shape2 = new Shape(
      new Point(1, 0),
      new Point(0, 1),
      fractal);

   fractal.shapes.pop();
   fractal.shapes.push(shape1);
   fractal.shapes.push(shape2);

   return fractal;
}

